import React, { useState } from 'react'

// components
import MuiAppBar from '@mui/material/AppBar';
import { 
    styled, 
  } from '@mui/material/styles';
import {  Drawer, IconButton, Toolbar } from '@mui/material';


// Images or Icons
import MenuRoundedIcon from '@mui/icons-material/MenuRounded';

const drawerWidth = 240;
// Custom Appbar
const AppBar = styled(MuiAppBar, {shouldForwardProp: (prop) => 
    prop !== 'open', })(({ theme, open }) => 
    ({
    transition: theme.transitions.create(['margin', 'width'], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
    }),
    ...(open && {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: `${drawerWidth}px`,
        transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
    }),
}),

}));

const Appbar = () => {
// Initializing variables
const [open ,setOpen] = useState(false)

// Intializing functions

const onClick_AppBar = () => {
// Appbar onClick function
    if (open) setOpen(false)
}

const onClick_Menu = e => {
// Menu onClick function
    e.preventDefault()
    if (open) setOpen(true)
}


  return (
    <div>

{/* Appbar */}
        <AppBar onClick={onClick_AppBar} position='fixed' open={open} >

        <Toolbar variant='dense'>
        

{/* Menu */}
            <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2, ...(open && { display: 'none' }) }}
            onClick={onClick_Menu}>

              <MenuRoundedIcon fontSize='large' />

            </IconButton>

            <Drawer
  // permanent
        variant="persistent"
        anchor="left"
        open={open}


        sx={{
          
          width: 0,
          flexShrink: drawerWidth,
          '& .MuiDrawer-paper': {
            width: drawerWidth,
            boxSizing: 'border-box',
          },
        }}>
            HEHEH

            </Drawer>
            </Toolbar>



        </AppBar>


    </div>
  )
}

export default Appbar